.table__icon--present {
  position: relative;
  display: block;
  margin: auto;
  width: 31px;
  height: 21px;
}

.table__icon--present::after, .table__icon--present::before {
  content: '';
  position: absolute;
  background: #3dc644;
}

.table__icon--present::before {
  top: 9px;
  left: 7px;
  width: 3px;
  height: 10px;
  transform: rotate(-45deg);
}

.table__icon--present::after {
  top: 1px;
  left: 17px;
  width: 3px;
  height: 20px;
  transform: rotate(45deg);
}

.table__icon--missing {
  position: relative;
  display: block;
  margin: auto;
  width: 31px;
  height: 21px;
}

.table__icon--missing::after, .table__icon--missing::before {
  content: '';
  position: absolute;
  top: 2px;
  left: 7px;
  background: #d22856;
}

.table__icon--missing::before {
  width: 3px;
  height: 20px;
  transform: rotate(-45deg);
}

.table__icon--missing::after {
  width: 3px;
  height: 20px;
  transform: rotate(45deg);
}

